.App {
  width: 1024px;
  height: 90vh;
  margin:16px auto;
  padding: 50px;
  border:1px solid gray;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}
